@import url(https://fonts.googleapis.com/css2?family=Fredoka:wght@400;500&family=Merriweather&family=Open+Sans&family=Oswald&family=Roboto+Serif:opsz,wght@8..144,100;8..144,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka:wght@400;500&family=Merriweather&family=Open+Sans&family=Oswald&family=Roboto+Serif:opsz,wght@8..144,100;8..144,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(0,0,0);
  background: radial-gradient(circle, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 0%, rgba(56,8,73,1) 0%, rgba(0,0,0,1) 100%, rgba(0,0,0,1) 100%);
   box-sizing: border-box;
   font-family: 'Fredoka', sans-serif;
font-family: 'Merriweather', serif;
font-family: 'Open Sans', sans-serif;
font-family: 'Oswald', sans-serif;
font-family: 'Roboto Serif', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.searchBar{
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
 
}

.input{
  width: 400px;
  height: 25px;
  border: none;
  background-color: darkgray;
  padding-left: 10px;
  border-radius: 50px;
  font-size: 20px;
}

.imgBar{
   max-width: 27px;
   background-color: darkgray;
   border-radius: 501px;
  
}

.App h1{
  color: white;
}

.cryptos{
  padding-top: 50px;
  display: grid;
  grid-template-columns: repeat(1,auto);
  grid-gap: 30px;
  gap: 30px;
}


.crypto{
  
 
  width: 700px;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  
  background-color: white;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  
  border-radius: 6px;
}

.img{
  max-width: 30px;
}
body{
  font-family: 'Fredoka', sans-serif;
  font-family: 'Merriweather', serif;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Oswald', sans-serif;
  font-family: 'Roboto Serif', serif;
}

.App {
  text-align: center;
  box-sizing: border-box;
  position: relative;
  justify-content: center;
  align-items: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


